// src/pages/settings.js
import React, { useState } from 'react';
import { useRouter } from 'next/router';
import { Button } from "@/components/ui/button"; //
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"; //
import { AlertTriangle, Loader2, LogOut, Trash2, Settings } from 'lucide-react'; //
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"; //

export default function SettingsPage() {
  const router = useRouter();
  const [deleting, setDeleting] = useState(false);

  const handleLogout = () => {
    localStorage.removeItem("token");
    router.push('/login');
  };

  const handleDeleteAccount = async () => {
    setDeleting(true);
    try {
      const token = localStorage.getItem("token");
      
      const res = await fetch('/api/user/delete', {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        }
      });

      if (!res.ok) {
        const errData = await res.json();
        throw new Error(errData.error || 'Failed to delete account');
      }

      // Success - Clear local storage and redirect
      localStorage.removeItem("token");
      alert("Your account has been successfully deleted.");
      router.push('/');

    } catch (err) {
      console.error(err);
      alert(`Error deleting account: ${err.message}`);
    } finally {
      setDeleting(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50/50 p-6 md:p-10">
      <div className="max-w-xl mx-auto">
        <h1 className="text-3xl font-bold mb-6 flex items-center">
          <Settings className="mr-3 h-6 w-6" /> Account Settings
        </h1>

        {/* Logout Card */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="text-xl flex items-center">
              <LogOut className="mr-2 h-5 w-5" /> Logout
            </CardTitle>
            <CardDescription>
              Sign out of your account on this device.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Button onClick={handleLogout} variant="secondary">
              Logout Now
            </Button>
          </CardContent>
        </Card>

        {/* Delete Account Card */}
        <Card className="border-red-500/50 bg-red-50/50">
          <CardHeader>
            <CardTitle className="text-xl flex items-center text-red-600">
              <Trash2 className="mr-2 h-5 w-5" /> Danger Zone
            </CardTitle>
            <CardDescription className="text-red-700">
              Permanently delete your account and all associated data. This action cannot be undone.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <AlertDialog>
              <AlertDialogTrigger asChild>
                <Button variant="destructive" disabled={deleting}>
                  {deleting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                  Delete My Account
                </Button>
              </AlertDialogTrigger>
              <AlertDialogContent>
                <AlertDialogHeader>
                  <AlertDialogTitle className="text-red-600 flex items-center">
                    <AlertTriangle className="mr-2 h-5 w-5" /> Confirm Account Deletion
                  </AlertDialogTitle>
                  <AlertDialogDescription>
                    Are you absolutely sure you want to delete your account? All your data, products, and order history will be permanently lost.
                  </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                  <AlertDialogCancel>Cancel</AlertDialogCancel>
                  <AlertDialogAction onClick={handleDeleteAccount} disabled={deleting} className="bg-red-600 hover:bg-red-700">
                    {deleting ? "Deleting..." : "Yes, Delete Account"}
                  </AlertDialogAction>
                </AlertDialogFooter>
              </AlertDialogContent>
            </AlertDialog>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}